import { _decorator, Component, Node } from "cc";
import GlobalData from "../home/GloabalClass";
import { basePageView } from "../common/basePageView";
import { basePageModel } from "../common/basePageModel";
import { inviteView } from "./inviteView";
const { ccclass, property } = _decorator;

export const taskUrls = {
  SearchTask: "https://api.infinitytest.cc/api/v1/task/list",
  ClaimReward: "https://api.infinitytest.cc/api/v1/task/claim",
  CompleteTask: "https://api.infinitytest.cc/api/v1/task/complete",
  CheckIn: "https://api.infinitytest.cc/api/v1/task/checkIn",
};

export interface taskListResponseType {
  code: string;
  data: {
    checkInList: {
      checkInNo: string;
      date: string;
      point: number;
      status: number;
    }[];
    gamePoint: number;
    taskModelResponses: {
      taskList: {
        buttonText: string;
        icon: string;
        remark: string;
        showMsg: string;
        taskDesc: string;
        taskId: number;
        taskLink: string;
        taskName: string;
        taskReward: number;
        taskType: number;
        value1: number;
        value2: string;
      }[];
      taskModelId: number;
      taskModelName: string;
      taskType: string;
    }[];
    totalInviteNum: number;
    totalPoint: number;
    twitterUserName: string;
  };
  message: string;
}

@ccclass("inviteModel")
export class inviteModel extends basePageModel {
    invitePage: inviteView
    inviteCode: any;
  start() {
    this.invitePage = this.view.getComponent(inviteView)
  }

  update(deltaTime: number) {}

  async initData(){
    this.inviteCode = GlobalData.inviteCode
    await this.getTaskList()
  }

  async getTaskList() {
        var config = {
            method: 'post',
            url: 'https://api.infinitytest.cc/api/v1/task/list',
            headers: { 
              'User-Agent': 'Apifox/1.0.0 (https://apifox.com)', 
              'Authorization': `Bearer ${GlobalData.token}`
            },
            params: {
              page: 'GAME_LOBBY',
              // 添加更多参数
            }
          };
        
        await (window.axios as any)(config)
        .then(function (taskList: taskListResponseType) {
           this.invitePage.setInviteInfo(taskList.data.totalInviteNum, taskList.data.gamePoint)
        })
        .catch(function (error) {
           console.log(error);
        });

    //   const taskList = await window.axios.post<taskListResponseType>(
    //     taskUrls.SearchTask,
    //     {
    //         page: 'GAME_LOBBY',
    //     },{
    //         headers: { 
    //               'Authorization': `Bearer ${GlobalData.token}`
    //         }
    //     }
    //   );

  }
}
